import http from '../../http-common'

class CountrySercice {
  getAll () {
    return http.get('Country')
  }

  getAllPagnation (params) {
    return http.get('Country', { params })
  }

  get (id) {
    return http.get(`Country/getById?Id=${id}`)
  }

  create (data) {
    return http.post('Country', data)
  }

  update (id, data) {
    return http.put(`Country?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`Country?Id=${id}`)
  }
}

export default new CountrySercice()
