import http from '../../http-common'

class CityService {
  getAll () {
    return http.get('City')
  }

  getAllPagnation (params) {
    return http.get('City', { params })
  }

  get (id) {
    return http.get(`City/getById?Id=${id}`)
  }

  create (data) {
    return http.post('City', data)
  }

  update (id, data) {
    return http.put(`City?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`City?Id=${id}`)
  }
}

export default new CityService()
