<template>
  <va-card :title="$t('tables.labelCity')" class="w-100">
    <va-button
      icon="fa fa-plus"
      flat
      slot="actions"
      class="mr-0"
      @click="showCreateModal"
    />
    <va-data-table
      :fields="fields"
      :data="dataTable"
      :loading="loading"
      :totalPages="totalPages"
      @page-selected="readItems"
      api-mode
      hoverable
    >
      <template slot="marker" slot-scope="props">
        {{props.rowIndex + 1}}
      </template>
      <template slot="country" slot-scope="props">
        {{props.rowData.country === null ? '-----': props.rowData.country.name}}
      </template>
      <template slot="actions" slot-scope="props">
        <va-button flat small color="gray" @click="showEditModal(props.rowData)" class="ma-0">
          {{ $t('tables.edit') }}
        </va-button>

        <va-button flat small color="danger" @click="showDeleteModal(props.rowData)" class="ma-0">
          {{ $t('tables.delete') }}
        </va-button>
      </template>
    </va-data-table>

    <va-modal v-model="deleteModal" no-outside-dismiss
      :title=" $t('generic.deleteItemTitle') "
      :okText=" $t('modal.delete') "
      :cancelText=" $t('modal.cancel') "
      :message=" $t('generic.deleteItemMsg') "
      @ok="okDeletefun()"
      @cancel="cancleDeletefun()">
    </va-modal>

    <va-modal v-model="createModal" no-outside-dismiss
      :title="$t(modelTitle)"
      hide-default-actions >
      <form @submit.prevent="editModal? okEditfun()  :okCreatefun()">
        <div class="row flex-center">
          <div class="flex md12 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.name')}} </label>
            <va-input v-model="currentobject.name"
              :error="riquierdvalidation(currentobject.name)"
              :error-messages="$t('errorMessage.required')"
              :placeholder="$t('placeholder.name')" required/>
          </div>
          <div class="flex md12 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.country')}} </label>
            <va-select
              :label="$t('placeholder.country')"
              v-model="fK_Country.value"
              required
              searchable
              textBy="name"
              keyBy="id"
              :options="fK_Country.options"
            />
          </div>
        </div>
        <div class="d-flex justify--center mt-3">
          <va-button type="button" color="gray" flat @click="editModal? cancleEditfun() : cancleCreatefun()">
            {{ $t('modal.cancel') }}
          </va-button>
          <va-button type="submit" :disabled="stopBtn" class="my-0">{{ $t(modelOktext) }}</va-button>
        </div>
      </form>

    </va-modal>
  </va-card>

</template>

<script>
import CityService from '../../../../services/BasicData/CityService'
import CountryService from '../../../../services/BasicData/CountryService'

import helper from '../../../../helper/helper'
export default {
  data () {
    return {
      modelTitle: '',
      modelOktext: '',
      stopBtn: false,
      perPage: 10,
      totalPages: 0,
      dataTable: [],
      deleteModal: false,
      editModal: false,
      createModal: false,
      loading: true,
      opjectBeforeEdit: {},
      currentobject: {},
      fK_Country: {
        value: '',
        options: [],
      },
    }
  },

  computed: {
    fields () {
      return [
        {
          name: '__slot:marker',
          width: '30px',
          dataClass: 'text-center',
        },
        {
          name: 'name',
          title: this.$t('tables.headings.name'),
        },
        {
          name: '__slot:country',
          title: this.$t('tables.headings.country'),
        },
        {
          name: '__slot:actions',
          dataClass: 'text-right',
        },
      ]
    },
  },
  created () {
    this.readItems()
    this.getCountry()
  },
  methods: {
    riquierdvalidation (data) {
      return helper.riquierdvalidation(data)
    },
    /*  api get */
    readItems (page = 1) {
      this.loading = true

      const params = {
        PageSize: this.perPage,
        PageNumber: page,
      }
      CityService.getAllPagnation(params).then(
        (response) => {
          this.dataTable = response.data
          var pagination = JSON.parse(response.headers['x-pagination'])
          this.totalPages = pagination.TotalPages
          this.loading = false
        },
        (err) => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        },
      )
    },
    getCountry () {
      CountryService.getAll()
        .then(response => {
          this.fK_Country.options = response.data
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        })
    },
    /*  end api get */
    /* cud api */
    create () {
      if (this.fK_Country.value !== '') {
        this.currentobject.fK_Country = this.fK_Country.value.id
      }
      CityService.create(this.currentobject).then(response => {
        if (response.headers['x-status'] === 'true') {
          this.readItems()
          this.loading = false
          this.currentobject = {}
          this.fK_Country.value = ''
          this.createModal = false
          this.stopBtn = false

          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-right',
            duration: 1500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',
        })
      })
    },
    edit (user) {
      if (this.fK_Country.value !== '') {
        this.currentobject.fK_Country = this.fK_Country.value.id
      }
      CityService.update(this.currentobject.id, this.currentobject).then(response => {
        if (response.headers['x-status'] === 'true') {
          this.readItems()
          this.fK_Country.value = ''
          this.currentobject = {}
          this.loading = false
          this.editModal = false
          this.createModal = false
          this.stopBtn = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-right',
            duration: 1500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',
        })
      })
    },
    remove (id) {
      CityService.delete(id).then(response => {
        if (response.headers['x-status'] === 'true') {
          const idx = this.dataTable.findIndex(u => u.id === id)
          this.dataTable.splice(idx, 1)
          this.loading = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-right',
            duration: 1500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',
        })
      })
    },
    /* cud api */

    /* Delete model */
    showDeleteModal (cuobject) {
      this.deleteModal = true
      this.currentobject = cuobject
    },
    okDeletefun () {
      this.remove(this.currentobject.id)
    },
    cancleDeletefun () {
      this.loading = false
    },
    /* end Delete model */

    /* edit model */
    showEditModal (cuobject) {
      this.editModal = true
      this.modelTitle = 'generic.editItemTitle'
      this.modelOktext = 'modal.edit'
      this.createModal = true
      this.fK_Country.value = cuobject.country
      this.opjectBeforeEdit = Object.assign({}, cuobject)
      this.currentobject = cuobject
      this.loading = true
    },
    okEditfun () {
      this.stopBtn = true
      this.edit(this.currentobject)
    },
    cancleEditfun () {
      Object.assign(this.currentobject, this.opjectBeforeEdit)
      this.createModal = false
      this.loading = false
      this.currentobject = {}
      this.fK_Country.value = ''
      this.editModal = false
      this.stopBtn = false
    },
    /* end edit model */

    /* create model */
    showCreateModal () {
      this.modelTitle = 'generic.creatItemTitle'
      this.modelOktext = 'modal.create'
      this.createModal = true
      this.loading = true
    },
    okCreatefun () {
      this.stopBtn = true
      this.create()
    },
    cancleCreatefun () {
      this.createModal = false
      this.loading = false
      this.fK_Country.value = ''

      this.currentobject = {}
      this.stopBtn = false
    },
    /* end create model */
  },
}

</script>

<style lang="scss" >
  i.fa.fa-exclamation {
    color: red !important;
  }

  .va-modal__inner {
    max-width: 100%;
  }

</style>
